.block {
    position: relative;
    padding: 1vw;
    margin: 0 auto;
    max-width: 85vw;
}
.link {
    position: absolute;
    left: 0;
    top: 2%;
    cursor: pointer;
    z-index: 1;
    border-bottom: #B92C25 1px solid;
}
.imgLink {
    width: 8vw;
    height: auto;
}
.wrapperBlock {
    background: url(../../assets/imgs/frezer.png) no-repeat center;
    background-size: contain;
}
.wrapperBlock h2 {
    position: relative;
    margin: .2vw;
    font-size: 2.9vw;
    color: #B92C25;

}
.wrapperBlock p {
    position: relative;
    font-size: 1.7vw;
    text-indent: 7%;
    text-align: justify;

}
