
.block {
    position: relative;
    padding: 1vw;
    margin: 0 auto;
    max-width: 300px;
}
.link {
    position: absolute;
    left: 0;
    top: 0;
    height: 19px;
    cursor: pointer;
    z-index: 1;
    border-bottom: #B92C25 1px solid;
}
.imgLink {
    width: 50px;
    height: auto;
}
.wrapperBlock {
    background: url(../../../assets/imgs/frezer.png) no-repeat center;
    background-size: contain;
}
.wrapperBlock h2 {
    position: relative;
    margin: 5px;
    font-size: 14px;
    color: #B92C25;

}
.wrapperBlock p {
    position: relative;
    font-size: 12px;
    text-indent: 25px;
    text-align: justify;

}
